<template>
  <div class="hello">
      <h1>
        <div class="animate  four">
			<span>З</span><span>а</span><span>б</span><span>ы</span>
			<span>л</span><span>и</span><span>&nbsp;</span><span>п</span><span>а</span><span>р</span><span>о</span>
      <span>л</span><span>ь</span>
      </div>
      </h1>
    <h3>Email адрес</h3>
    <h4>Получить новый пароль просто: <br><br>
      1. Введите адрес электронной почты, который вы указывали при регистрации <br><br>
      2. Нажмите кнопку "Отправить" и проверьте свою почту<br><br>
    </h4>
    <div class="group">
      <input v-model="email" type="text" inputmode="email" required />
      <span class="highlight"></span>
      <span class="bar"></span>
      <label>Email</label>
    </div>
    <button class="btn_reg button" @click="signup(name, password,email,userN)">
      Отправить
    </button>
    <div>
    <router-link to="/">
    <button  class="button">
     Назад
    </button>
    </router-link>
    </div>
  </div>
</template>

<script>
import router from '../router/index.js';
import api from "../assets/js/api.js";
export default {
  name: "forget",
  data() {
    return {
      name: "",
      password: "",
      email: "",
      userN: "",
    };
  },
  computed:{
    auth(){
      return this.$store.getters.get_auth;
    },
    curname(){
      return this.$store.getters.get_name;
    },
    code(){
      return this.$store.getters.get_code;
    }
    },
  watch: {
    auth: function (val) {
      if(val){
      this.$store.commit('change_name',this.curname)
      this.$store.commit('change_code',this.code)
      this.$toast.success(`Вы уже вошли в систему, прежде чем войти, выйдите из текущего пользователя`);
      router.push(`home`);
    }
    }
    },
  methods: {
    async signup(name, password,email,userN) {
      if(password.length<6){
         this.$toast.error(`Пароль должен быть не менее 6 символов`);
      }else{
      let msg = await api.signup({ name, password ,email,userN});
      if(msg.token){
       this.$toast.success(`Поздравляем, вы успешно зарегистрировались,осталось только войти в систему под своим именем и паролем.`);
       router.push('/');
      }else{
       this.$toast.error(`Попробуйте снова, кажется вы уже зарегистрированы или такой пользователь существует`);
      }
      }
    },
  },
};
</script>

<style >
.animate {
	font-size: 36px;
}
.animate span:nth-of-type(2) {
	animation-delay: .05s;
}
.animate span:nth-of-type(3) {
	animation-delay: .1s;
}
.animate span:nth-of-type(4) {
	animation-delay: .15s;
}
.animate span:nth-of-type(5) {
	animation-delay: .2s;
}
.animate span:nth-of-type(6) {
	animation-delay: .25s;
}
.animate span:nth-of-type(7) {
	animation-delay: .3s;
}
.animate span:nth-of-type(8) {
	animation-delay: .35s;
}
.animate span:nth-of-type(9) {
	animation-delay: .4s;
}
.animate span:nth-of-type(10) {
	animation-delay: .45s;
}
.animate span:nth-of-type(11) {
	animation-delay: .5s;
}
.animate span:nth-of-type(12) {
	animation-delay: .55s;
}
.animate span:nth-of-type(13) {
	animation-delay: .6s;
}
.animate span:nth-of-type(14) {
	animation-delay: .65s;
}
.animate span:nth-of-type(15) {
	animation-delay: .7s;
}
.animate span:nth-of-type(16) {
	animation-delay: .75s;
}
.animate span:nth-of-type(17) {
	animation-delay: .8s;
}
.animate span:nth-of-type(18) {
	animation-delay: .85s;
}
.animate span:nth-of-type(19) {
	animation-delay: .9s;
}
.animate span:nth-of-type(20) {
	animation-delay: .95s;
}
.animate span {
	display: inline-block;
}
.four span {
	color: #fff;
	opacity: 0;
	transform: translate(0, -100px) rotate(360deg) scale(0);
	animation: revolveDrop 0.3s  forwards;
}


@keyframes revolveDrop {
	30% {
		transform: translate(0, -50px) rotate(180deg) scale(1);
	}

	60% {
		transform: translate(0, 20px) scale(.8) rotate(0deg);
	}

	100% {
		transform: translate(0) scale(1) rotate(0deg);
		opacity: 1;
	}
}

</style>
